import { writable } from "svelte/store";
const initialState = {
    name: "",
    key: "",
    rol: "2",
    sessionTime: "",
    routes: [],
    actions: []
};
function creatUserProfileStore() {
    const { subscribe, set, update } = writable(initialState);
    return {
        subscribe,
        setProfile: (userApp) => update((s) => (Object.assign(Object.assign({}, s), { name: userApp.user, key: userApp.key, rol: userApp.rol, routes: userApp.routes, sessionTime: userApp.sessionTime }))),
        setSessionTime: (sessionTime) => update((s) => (Object.assign(Object.assign({}, s), { sessionTime: sessionTime }))),
        setActions: (actions) => update((s) => (Object.assign(Object.assign({}, s), { actions: actions }))),
        reset: () => set(initialState)
    };
}
export default creatUserProfileStore();
