import { writable } from 'svelte/store';
const defaultState = {
    isCollapsed: true,
    show: true
};
function createFileToolbarStore() {
    const { update, subscribe } = writable(defaultState);
    return {
        subscribe,
        setCollapse: (isCollapsed) => update((s) => (Object.assign(Object.assign({}, s), { isCollapsed: isCollapsed }))),
        setShow: (show) => update((s) => (Object.assign(Object.assign({}, s), { isCollapsed: true, show: show }))),
    };
}
export default createFileToolbarStore();
