const IMG_PREVIEW = ["jpg", "png", "jpeg", "svg", "gif", "webp"];
const FILE_AS_TEXT = ["css", "md", "svelte", "ts", "java", "json", "js", "txt", "yml"];
const EXCEL = ["csv", "ods", "pods", "xls", "xlsm", "xlsx"];
const WORD = ["doc", "docx"];
const AUDIO = ["mp3", "flac", "wav"];
