import { secure } from "../helpers/Misc";
import httpClient from "./HttpClient";
const AdminService = {
    list: () => {
        return httpClient.getJson(`admin/users/list`);
    },
    add: (data, cb, err) => {
        let userData = Object.assign(Object.assign({}, data), { key: secure.digest(data.key), routes: data.routes.map(r => secure.digest(r)) });
        httpClient.post(`admin/users/add`, { userData })
            .then(cb)
            .catch(err);
    },
    edit: (data, cb, err) => {
        let userData = Object.assign(Object.assign({}, data), { key: secure.digest(data.key), routes: data.routes.map(r => secure.digest(r)) });
        httpClient.post(`admin/users/edit`, { userData })
            .then(cb)
            .catch(err);
    },
    delete: (data, cb, err) => {
        httpClient.post(`admin/users/delete`, { userData: { user: data.user } })
            .then(cb)
            .catch(err);
    }
};
export default AdminService;
