import { writable } from 'svelte/store';
import FileBrowser from '../constants/FileBrowser';
const defaultState = {
    showForm: false,
    formTitle: "",
    formIcon: "",
    groupTarget: null,
    fileTarget: null,
    groupList: getLocalBookmarkGroups()
};
function getLocalBookmarkGroups() {
    let data = localStorage.getItem(FileBrowser.localStorageKeys.bookmarksGroups);
    if (!data) {
        return [];
    }
    let groups = JSON.parse(data);
    return groups;
}
function setLocalBookmarkGroups(groupList) {
    localStorage.setItem(FileBrowser.localStorageKeys.bookmarksGroups, JSON.stringify(groupList));
    return groupList;
}
function createFileBookmarkGroupStore() {
    const { update, subscribe } = writable(defaultState);
    return {
        subscribe,
        showForm: () => update((s) => (Object.assign(Object.assign({}, s), { showForm: true, formTitle: "Nuevo grupo", formIcon: "fas fa-folder-plus" }))),
        setFileTarget: (fileTarget) => update((s) => (Object.assign(Object.assign({}, s), { showForm: true, formTitle: "Nuevo marcador", formIcon: "fas fa-star", fileTarget: fileTarget }))),
        hideForm: () => update((s) => (Object.assign(Object.assign({}, s), { showForm: false, formTitle: "", formIcon: "", fileTarget: null, groupTarget: null }))),
        setGroupTarget: (groupTarget) => update((s) => (Object.assign(Object.assign({}, s), { showForm: true, formTitle: "Editar grupo", formIcon: "fas fa-edit", groupTarget }))),
        setGroupList: (groupList) => update((s) => (Object.assign(Object.assign({}, s), { groupList: setLocalBookmarkGroups(groupList) }))),
    };
}
export default createFileBookmarkGroupStore();
