<script lang="ts">
    import { onMount } from "svelte";
    import UserService from "../services/UserService";
    import fileBookmarkGroupStore from "../stores/fileBookmarkGroupStore";
    import dialogStore from "../stores/dialogStore";
    import fileSettingStore from "../stores/fileSettingStore";
    import userProfileStore from "../stores/userProfileStore";
    import fileBrowserStore from "../stores/fileBrowserStore";
    import appViewStore from "../stores/appViewStore";
    import InputText from "./commons/InputText.svelte";
    import type { Login } from "../types/UITypes";

    let finalError: string = "";
    let values: Login = { user: "", key: "" };
    let errors: Login = { user: "", key: "" };

    function handleSubmit() {
        dialogStore.showLoading();
        UserService.login(
            values,
            (data) => {
                fileSettingStore.initCache(data.routes);
                userProfileStore.setProfile({
                    ...data,
                    user: values.user,
                    key: values.key,
                });
                fileBookmarkGroupStore.setGroupList(data.bookmarksGroup);
                fileBrowserStore.setBookmarks(data.bookmarks);
                dialogStore.closeDialog();
                console.log($appViewStore.fordward)
                appViewStore[$appViewStore.fordward]();
            },
            (err) => dialogStore.showMessage(err.message)
        );
    }
    onMount(() => {
        document.title = `FileBrowser - Login`;
    });
</script>

<div class="login">
    <fieldset class="login-container">
        <legend>File Browser</legend>
        <div class="login-section">
            <i class="fas fa-key login-icon" />
        </div>
        <div class="login-section">
            <form on:submit|preventDefault={handleSubmit}>
                <InputText
                    name="user"
                    label="Usuario"
                    bind:value={values.user}
                    bind:errors={errors.user}
                />
                <InputText
                    name="key"
                    label="Password"
                    type="password"
                    bind:value={values.key}
                    bind:errors={errors.key}
                />
                {#if finalError}
                    <div class="f-08" style="color: red">
                        * {finalError}
                    </div>
                {/if}
                <div class="form-field-control d-flex">
                    <button type="submit" class="btn m-auto w-50">
                        Ingresar
                    </button>
                </div>
            </form>
        </div>
    </fieldset>
</div>

<style lang="scss">
    @import "../styles/vars";
    .login {
        height: 100%;
        display: flex;
        fieldset {
            border: 0px;
            border-top: 1px solid $border-light;
            padding: 1rem;
            legend {
                margin: auto;
                font-size: 1.25rem;
            }
        }
        &-container {
            max-width: 600px;
            margin: auto;
            display: flex;
        }
        &-section {
            min-width: 280px;
            max-width: 25%;
            margin: auto;
        }
        &-icon {
            font-size: 3rem;
        }
    }
    @media (max-width: $responsive-size) {
        .login {
            &-container {
                max-width: 320px;
                margin: auto;
                display: flex;
                flex-direction: column;
            }
        }
    }
</style>
