import { writable } from "svelte/store";
const initialState = {
    browser: true,
    login: false,
    userControl: false,
    fordward: "browser"
};
function createAppViewStore() {
    const { subscribe, set, update } = writable(initialState);
    return {
        subscribe,
        login: (fordward = "browser") => update(s => (Object.assign(Object.assign({}, s), { login: true, browser: false, userControl: false, fordward }))),
        browser: () => update(s => (Object.assign(Object.assign({}, s), { login: false, browser: true, userControl: false }))),
        config: () => update(s => (Object.assign(Object.assign({}, s), { login: false, browser: false, userControl: true }))),
        reset: () => set(initialState)
    };
}
export default createAppViewStore();
