import FileBrowser from "../constants/FileBrowser";
import { secure } from "../helpers/Misc";
let token = { "Authorization": "Bearer " + sessionStorage.browserToken };
function generateUrl(base, params, key) {
    let url = new URL(base);
    if (params)
        params.forEach((val, key) => url.searchParams.append(key, encodeURIComponent(val)));
    if (key) {
        url.searchParams.append("tmp", encodeURIComponent(secure.digest(sessionStorage.browserToken)));
    }
    return url.toString();
}
async function getData(path, params, typeResponse) {
    let url = generateUrl(`${FileBrowser.baseUrl}/${path}`, params, false);
    const response = await fetch(url, {
        method: 'GET',
        mode: 'cors',
        cache: 'no-cache',
        credentials: 'same-origin',
        headers: token,
        redirect: 'follow',
        referrerPolicy: 'no-referrer',
    });
    return validateResponse(response, typeResponse);
}
async function postData(path = '', data = {}, typeResponse) {
    const response = await fetch(`${FileBrowser.baseUrl}/${path}`, {
        method: 'POST',
        mode: 'cors',
        cache: 'no-cache',
        credentials: 'same-origin',
        headers: Object.assign({ 'Content-Type': 'application/json' }, token),
        redirect: 'follow',
        referrerPolicy: 'no-referrer',
        body: JSON.stringify(data)
    });
    return validateResponse(response, typeResponse);
}
async function validateResponse(response, dataType = "json") {
    if (response.status === 200) {
        return response[dataType]();
    }
    else {
        let error = await response.json();
        throw processErrorApiResponse(error);
    }
}
function processErrorApiResponse(err) {
    if (err.secure) {
        err.message = secure.recover(err.message);
        if (err.errors) {
            err.errors = err.errors.map((e) => ({
                route: secure.recover(e.route),
                name: secure.recover(e.name),
                message: secure.recover(e.message)
            }));
        }
    }
    return err;
}
const httpClient = {
    setToken: (t) => {
        if (t) {
            token = { "Authorization": "Bearer " + t };
            sessionStorage.browserToken = t;
        }
    },
    getImageUrl: (path, params) => generateUrl(`${FileBrowser.baseUrl}/${path}`, params, false),
    getAuthUrl: (path, params) => generateUrl(`${FileBrowser.baseUrl}/${path}`, params, true),
    getTxt: async (path, params) => getData(path, params, "text"),
    getJson: async (path, params = null) => getData(path, params, "json"),
    post: async (path = '', data = {}) => postData(path, data, "json"),
    postDownload: async (path = '', data, error = (data) => null) => postData(path, data, "blob"),
    postForm: async (path = '', data) => {
        const response = await fetch(`${FileBrowser.baseUrl}/${path}`, {
            method: 'POST',
            mode: 'cors',
            cache: 'no-cache',
            credentials: 'same-origin',
            headers: token,
            redirect: 'follow',
            referrerPolicy: 'no-referrer',
            body: data
        });
        return validateResponse(response);
    },
};
export default httpClient;
